a {
  text-decoration: none;
  color: $black;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
body,
ul,
li,
p {
  padding: 0;
  margin: 0;
  list-style: none;
  border: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
